import React, {Component} from "react";
import {connect} from "react-redux";
import {getSelectedOfferType} from "../../../configurator/selectors/filters";
import OfferType from "../../constants/OfferTypeEnum";
import {getMarketContextPrefixUrl,getProductListUrl} from "eshop/modules/configurator/selectors/metaData"
import {isDuet} from "../../selectors";
import MarketContextCheckboxView from "../../../configurator/components/MarketContextCheckboxView";

class ProductDetailsBackToListComponent extends Component {
    constructor(props) {
        super(props);
    }

    addFilterStateIfNeeded(returnLink) {
        let filterState = "";
        if (this.props.filterState !== null && this.props.filterState !== "" && this.props.filterState !== undefined) {
            filterState =  (returnLink.includes("?") ? "&" : "?") + this.props.filterState;
        }
        return filterState;
    }

    render() {
        let returnLink = this.props.marketContextPrefixUrl + getProductListUrl;
        if(this.props.isDuet){
            returnLink = returnLink + "?isDuet=true";
        }
        return (
            <div className="l-full-row">
                <div className="l-page-row u-spacing-top-m">
                    <div className={"l-row"}>
                        <div className="l-col l-col-8 l-col-medium-12 l-col-small-12">
                    <span className="u-left">

                    {this.props.selectedOfferType === OfferType.CONVERGENT ?
                        <a href={"javascript:history.back()"} className="u-text-decoration-none">
                            {this.props.linkName}
                        </a>
                        :
                        <a href={returnLink + this.addFilterStateIfNeeded(returnLink)} className="u-text-decoration-none">
                            {this.props.linkName}
                        </a>
                    }
                    </span>
                        </div>
                        <div className="l-col l-col-4 l-col-medium-12 l-col-small-12 u-right ">
                        <span className="u-right">
                        <MarketContextCheckboxView/>
                    </span>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    selectedOfferType: getSelectedOfferType(state),
    marketContextPrefixUrl:getMarketContextPrefixUrl(state),
    isDuet: isDuet(state),
});

export default connect(mapStateToProps)(ProductDetailsBackToListComponent);
